body {
  margin: 0;
  padding: 0;
  background-color: #123456;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#AppRoot {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

* {
  box-sizing: border-box;
}

.App {
  overflow: hidden;
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: 400px 1fr;
  grid-template-rows: 100vh;
  grid-template-areas: "debugger logger";
  background-color: #234567;
  color: #fff; }

@-webkit-keyframes RotationAnimation {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg); } }

@keyframes RotationAnimation {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg); } }

.ActionLogs {
  grid-area: logger;
  background-color: #0d253d;
  border-left: 2px solid #456789;
  display: grid;
  grid-template-rows: 60px 1fr;
  grid-template-areas: "header" "logs"; }
  .ActionLogs .LogsHeader {
    background-color: rgba(0, 0, 0, 0.2);
    grid-area: header;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    padding: 10px 30px; }
    .ActionLogs .LogsHeader .ConnectionInformation {
      font-size: 11px;
      text-transform: uppercase; }
      .ActionLogs .LogsHeader .ConnectionInformation .Network {
        font-weight: 600;
        color: #EB6B56;
        margin-left: 5px; }
    .ActionLogs .LogsHeader .ConnectionLoader {
      display: -webkit-flex;
      display: flex;
      -webkit-align-items: center;
              align-items: center; }
      .ActionLogs .LogsHeader .ConnectionLoader .ZippoIcon {
        -webkit-transform-origin: center;
                transform-origin: center;
        display: block;
        color: #FFC153;
        -webkit-animation: RotationAnimation 1s linear infinite;
                animation: RotationAnimation 1s linear infinite; }
      .ActionLogs .LogsHeader .ConnectionLoader span {
        margin-left: 10px;
        font-size: 11px;
        text-transform: uppercase; }
  .ActionLogs .LogsContent {
    grid-area: logs;
    overflow-y: auto; }
  .ActionLogs .LogsWrapper {
    padding: 10px 15px; }
  .ActionLogs .LogDivider:not(:last-child) {
    margin: 5px 0;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1); }
  .ActionLogs .LogEntry {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
    cursor: pointer;
    padding: 10px 15px;
    border-radius: 5px;
    transition: 0.2s; }
    .ActionLogs .LogEntry .BasicInfo {
      -webkit-flex: 0 0 140px;
              flex: 0 0 140px;
      margin-right: 20px;
      padding-top: 1px; }
      .ActionLogs .LogEntry .BasicInfo .LogLevel {
        text-transform: uppercase;
        font-size: 13px;
        margin-bottom: 5px; }
      .ActionLogs .LogEntry .BasicInfo .LogTimestamp {
        font-size: 11px;
        opacity: 0.5; }
    .ActionLogs .LogEntry .LogDetails {
      -webkit-flex: 1 1;
              flex: 1 1;
      padding: 7px 0;
      font-size: 12px; }
      .ActionLogs .LogEntry .LogDetails .MainMessage {
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: space-between;
                justify-content: space-between;
        -webkit-align-items: center;
                align-items: center; }
        .ActionLogs .LogEntry .LogDetails .MainMessage .ZippoIcon {
          font-size: 20px;
          opacity: 0;
          transition: 0.2s; }
      .ActionLogs .LogEntry .LogDetails .LogType {
        opacity: 0.75;
        margin-right: 10px; }
    .ActionLogs .LogEntry.Error .LogLevel, .ActionLogs .LogEntry.Error .MainMessage {
      color: #ea5455; }
    .ActionLogs .LogEntry:hover {
      background-color: rgba(0, 0, 0, 0.1); }
      .ActionLogs .LogEntry:hover .MainMessage .ZippoIcon {
        opacity: 0.75;
        color: #fff; }

